body {
  color: #000;
  background: #fff;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.out {
  display: flex;
  height: 100vh;
}

.out__aside {
  flex: 0 0 auto;
  width: 275px;
  background: #343B43;
}

.out__container {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}

.main {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  background: url("../img/Working-Environment.jpg") no-repeat center center;
  background-size: cover;
}

.main_simple {
  background: #fff;
}

.inner {
  position: relative;
  flex: 1 1 auto;
  overflow: auto;
  padding: 16px;
  background: rgba(60, 67, 77, 0.6);
}

.inner_simple {
  background: none;
}

.inner_darken {
  background: rgba(60, 67, 77, 0.9);
}

.inner_center {
  text-align: center;
  font-size: 0;
}

.inner_center:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.inner_center .container {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  text-align: left;
}

.btn {
  padding: 0 10px;
  border: 1px solid #134580;
  border-radius: 2px;
  min-width: 150px;
  color: #fff;
  background: #1B579E;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: inherit;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 53px;
  text-align: center;
}

.btn_secondary {
  border-color: #357EC0;
  background: #20B3F3;
}

.btn_primary {
  border-color: #105897;
  background: #3185D0;
}

.btn_sm {
  min-width: 125px;
  height: 45px;
  font-size: 18px;
  font-weight: 500;
  line-height: 43px;
  letter-spacing: 0.02em;
  box-shadow: none;
}

.btn_lg {
  height: 61px;
  line-height: 59px;
}

.switcher {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  cursor: pointer;
}

.switcher__input {
  display: none;
}

.switcher__input:checked ~ .switcher__icon:after {
  transform: translateX(20px);
}

.switcher__icon {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 40px;
  height: 20px;
}

.switcher__icon:before, .switcher__icon:after {
  content: '';
  position: absolute;
  top: 50%;
  background: #3185D0;
}

.switcher__icon:before {
  left: 3px;
  right: 3px;
  margin-top: -7px;
  border-radius: 7px;
  height: 14px;
  opacity: 0.5;
}

.switcher__icon:after {
  left: 0;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  transition: transform 0.25s;
}

.switcher__text {
  display: inline-block;
  vertical-align: top;
  font-family: inherit;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.25;
  letter-spacing: 0.02em;
}

.switcher__right {
  margin-left: 10px;
}

.switcher__left {
  margin-right: 10px;
}

.select {
  display: inline-block;
  vertical-align: top;
  padding: 0 16px;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  background-image: url("../img/angle-down.svg");
  background-repeat: no-repeat;
  background-position: right 15px center;
  background-size: auto 18px;
  font-family: inherit;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.02em;
  appearance: none;
}

.select::ms-expand {
  display: none;
}

.select_lg {
  height: 61px;
  font-size: 20px;
}

.input {
  display: inline-block;
  vertical-align: top;
  padding: 0 16px;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  font-family: inherit;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.02em;
  appearance: none;
}

.input_sm {
  padding: 0 20px;
  height: 40px;
  font-size: 18px;
}

.input_sm-width {
  max-width: 210px;
}

.message {
  color: #1F7212;
  padding: 7px 0;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.02em;
}

.password-box {
  position: relative;
}

.password-box__icon {
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -13px;
  width: 26px;
  height: 26px;
  background: url("../img/fingerprint.svg") no-repeat center center;
  background-size: contain;
}

.field__label {
  display: block;
  padding: 8px 0;
  color: #000;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.field__label_lg {
  font-size: 18px;
  text-transform: none;
}

.field__message {
  margin-top: 3px;
  text-align: center;
}

.alert {
  position: relative;
  padding: 9px 20px;
  display: block;
  margin: 0 auto;
  width: 374px;
  border-radius: 6px;
  color: #fff;
  background: #6CBB5F;
  font-family: inherit;
  font-size: 18px;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.02em;
}

.alert:not(:first-child) {
  margin-top: 21px;
}

.alert__close {
  position: absolute;
  top: -9px;
  right: -12px;
  width: 25px;
  height: 25px;
  color: #000;
  background: none;
  text-align: center;
  font-size: 0;
}

.alert__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.alert__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 17px;
}

.header {
  flex: 0 0 auto;
  padding: 10px 16px;
  background: #3185D0;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__nav-btn {
  position: relative;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 7px;
  width: 50px;
  height: 50px;
  background: none;
}

.header__nav-btn:before, .header__nav-btn:after,
.header__nav-btn span {
  display: block;
  border-radius: 3px;
  width: 36px;
  height: 6px;
  background: #fff;
}

.header__nav-btn:before, .header__nav-btn:after {
  content: '';
}

.menu__header {
  padding: 15px;
  margin-right: 3px;
  color: #fff;
  background: #3185D0;
}

.menu__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.02em;
}

.menu__meta {
  font-size: 14px;
  line-height: 20px;
}

.menu__item:not(:first-child) {
  border-top: 1px solid #484F59;
}

.menu__link {
  position: relative;
  display: block;
  text-decoration: none;
}

.menu__link.is-active {
  background: #484F59;
}

.menu__link-icon {
  position: absolute;
  top: 50%;
  left: 9px;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 40px auto;
}

.menu__link-icon_dashboard {
  background-image: url("../img/dashboard.png");
  background-size: 50px auto;
}

.menu__link-icon_timesheet {
  background-image: url("../img/timesheet.png");
  background-size: 50px auto;
}

.menu__link-icon_time-adjustments {
  background-image: url("../img/time-adjustments.png");
}

.menu__link-icon_help {
  background-image: url("../img/help.png");
}

.menu__link-text {
  display: block;
  padding: 19px 15px 19px 61px;
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.02em;
}

.form-box {
  margin-left: auto;
  margin-right: auto;
  padding: 26px 67px 44px;
  border: 2px solid #6E6E6E;
  border-radius: 5px;
  width: 100%;
  max-width: 488px;
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.form-box__logo {
  display: block;
  margin: 0 auto 11px;
  width: 90px;
}

.form-box__logo-img {
  width: 100%;
}

.form-box__title {
  margin-bottom: 7px;
  font-weight: 400;
  font-size: 24px;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.02em;
}

.form-box__field:not(:last-child) {
  margin-bottom: 16px;
}

.form-box__footer {
  margin: 9px -16px -16px;
  text-align: center;
}

.form-box__btn {
  margin: 16px;
}

.box {
  padding: 31px 35px 24px;
  margin: 0 auto;
  border: 1px solid rgba(0, 0, 0, 0.57);
  border-radius: 3px;
  max-width: 990px;
  background: #fff;
}

.box__field {
  margin-bottom: 16px;
}

.box__footer {
  margin-top: 24px;
}

.frame {
  padding: 24px;
  margin: 0 auto;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  max-width: 1104px;
  background: #ECECEC;
}

.frame__inner {
  margin: 0 auto;
  max-width: 965px;
}

.frame__row {
  margin-bottom: 44px;
}

.frame__row:after {
  content: " ";
  display: table;
  clear: both;
}

.frame__date {
  float: left;
  margin-right: 56px;
  width: 251px;
}

.frame__clock-in {
  margin-top: 13px;
}

.frame__location {
  float: right;
  margin-top: 13px;
  width: 251px;
}

.frame__information {
  margin-bottom: 53px;
}

.date {
  padding: 13px;
  border: 1px solid #D6D6D6;
  border-radius: 4px;
  background: #fff;
  color: #3C434D;
  line-height: 1.25;
  text-align: center;
}

.date__title {
  font-size: 30px;
}

.date__meta {
  font-size: 16px;
}

.information {
  display: flex;
  border: 1px solid #D6D6D6;
  color: #3C434D;
  background: #fff;
}

.information__status, .information__box {
  flex: 1 1 50%;
}

.information__box {
  padding: 20px 15px 37px;
}

.information__status {
  position: relative;
  padding: 15px;
}

.information__status:not(:last-child):before {
  content: '';
  position: absolute;
  top: 32px;
  right: 0;
  bottom: 32px;
  border-right: 1px solid #000000;
}

.information__title {
  padding: 15px;
  font-weight: 500;
  font-size: 26px;
  line-height: 1;
}

.information__value, .information__label, .information__subtitle {
  font-size: 22px;
  line-height: 1;
}

.information__value {
  flex: 1 1 auto;
}

.information__value-item:not(:last-child) {
  margin-bottom: 20px;
}

.information__label {
  flex: 0 0 auto;
  padding-right: 30px;
}

.information__text {
  display: flex;
  padding: 15px;
}

.information__subtitle {
  margin-bottom: 17px;
  text-align: center;
}

.information__row {
  margin: 0 auto;
  max-width: 339px;
  display: flex;
  justify-content: space-between;
}

.time-box {
  padding-bottom: 5px;
  border: 2px solid #343B43;
  border-radius: 2px;
  width: 135px;
  background: #fff;
  text-align: center;
}

.time-box__title {
  padding: 7px;
  margin-bottom: 4px;
