}

.grid-table {
  overflow: hidden;
  border: 1px solid #E1E1E1;
  border-radius: 2px;
}

.grid-table__header, .grid-table__row {
  display: flex;
}

.grid-table__col {
  padding: 15px;
  border: 1px solid #E1E1E1;
  background: #fff;
  color: #3C434D;
  font-size: 20px;
  text-align: center;
  line-height: 1;
}

.grid-table__col_one-six, .grid-table__col_date {
